nav {
    background: #fff;
    box-shadow: var(--box-shadow);
    height: 3.2em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 1em;
    a {
        margin: .5em;
        display: flex;
    }
    .menu {
        display: flex;
    }
}

#App {
    .user-icon {
        font-size: 2em;
        border: 2px solid var(--yellow);
        width: 1.2em;
        height: 1.2em;
        box-sizing: border-box;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50px;
    }
}